import {
	request
} from './request.js'

const myApi = {
	// 
	driver(params) {
		return request({
			method: 'Get',
			url: '/api/driver',
			params: params
		})
	},
	// 
	driveraccount(params) {
		return request({
			method: 'Get',
			url: '/api/driver/account',
			params: params
		})
	},
	// 
	listWithdraw(params) {
		return request({
			method: 'Get',
			url: '/api/driver/account/listWithdraw',
			params: params
		})
	},
	// 
	listAccout(params) {
		return request({
			method: 'Get',
			url: '/api/driver/account/listAccout',
			params: params
		})
	},
	// 
	withdraw(params) {
		return request({
			method: 'Get',
			url: '/api/driver/account/withdraw',
			params: params
		})
	},
	// 
	close(params) {
		return request({
			method: 'Get',
			url: '/api/driver/close',
			params: params
		})
	},
	// 
	open(params) {
		return request({
			method: 'Get',
			url: '/api/driver/open',
			params: params
		})
	},

	// 
	driverMessagelist(params) {
		return request({
			method: 'Get',
			url: '/api/driver/driverMessage/list',
			params: params
		})
	},
	driverMessage(params) {
		return request({
			method: 'Get',
			url: '/api/driver/driverMessage/' + params,
		})
	},
	
	//  1234
	question(params) {
		return request({
			method: 'Get',
			url: '/api/cms/question/type',
			params: params
		})
	},
	// code
	aboutUs(params) {
		return request({
			method: 'Get',
			url: `/api/cms/richtext/${params.code}`,
		})
	},
}


export default myApi
