import {
	request
} from './request.js'

const orderApi = {
	// 
	grabOrderList(params) {
		return request({
			method: 'Get',
			url: '/api/driver/order/grabOrderList',
			params: params
		})
	},
	// 
	grabOrder(params) { 
		return request({
			method: 'Get',
			url: '/api/driver/order/grabOrder/' + params,
		})
	},
	// 
	order(params) {
		return request({
			method: 'Get',
			url: '/api/driver/order/'+params.type,
			params: params
		})
	},
	// 
	orderDetails(params) {
		return request({
			method: 'Get',
			url: '/api/driver/order/orderDetails/'+params,
		})
	},
	// 
	pickCar(params) {
		return request({
			method: 'Postquery',
			url: '/api/driver/order/pickCar',
			params: params
		})
	},
	// 
	goPickCar(params) {
		return request({
			method: 'Get',
			url: '/api/driver/order/goPickCar/'+params,
		})
	},
	// 
	goCar(params) {
		return request({
			method: 'Get',
			url: '/api/driver/order/goCar/'+params,
		})
	},
	// 
	returnCar(params) {
		return request({
			method: 'Get',
			url: '/api/driver/order/returnCar/'+params.id+'/'+params.code,
		})
	},
}


export default orderApi
