// 
var header = 'application/x-www-form-urlencoded'
var headerPost = 'application/json'
// 
export function request({
	method,
	url,
	params,
	loading
}) {

	        var url1 = 'http://hnhlyt.ticp.net' + url
	       //var url1 = 'http://hnhlyt.ticp.net/prod-api' + url
	console.log('url1',url1);





	if (!loading) {
		uni.showLoading({
			title: '...',
			icon: 'loading',
			mask: true
		});
	}
	console.log('sssssssssssss',method);
	if (method == 'Get') {
		return get(url1, params)
	} else if (method == 'Post') {
		return post(url1, params)
	} else if (method == 'Postquery') {
		return postquery(url1, params)
	}
}

// get
function get(url, params) {
	var Authorization = uni.getStorageSync('Authorization');
	return new Promise((resolve, reject) => {
		uni.request({
			url: url, //
			method: "GET",
			data: params,
			dataType: 'json',
			header: {
				'content-type': header,
				'Authorization': Authorization
			},
			success: (res) => {
				uni.hideLoading();
				if(res.data.code==200){
					resolve(res.data);
				}else{
					erry(res)
					reject(res.data)
				}
				
			},
			fail: (err) => {
				uni.hideLoading();
				if (err) {
					uni.showToast({
						title: err.data,
						icon: "none",
						duration: 2000
					});
					reject(err.data)
				} else {
					uni.showToast({
						title: '',
						duration: 2000,
						icon: 'none',
					});
					reject(new Error(''))
				}
			}
		});
	})
}

// post
function post(url, params) {
	var Authorization = uni.getStorageSync('Authorization');
	var utoken = ''
	if(params&&params.utoken){
		utoken=params.utoken
	}
	return new Promise((resolve, reject) => {
		uni.request({
			url: url, //
			method: "POST",
			data: params,
			header: {
				'utoken': utoken,				
				'content-type': headerPost,
				'Authorization': Authorization
			},
			success: (res) => {
				uni.hideLoading();
				console.log(res.data.code)
				if(res.data.code==200){
					resolve(res.data);
				}else{
					erry(res)
					reject(res.data)
				}
			},
			fail: (err) => {
				uni.hideLoading();
				if (err) {
					uni.showToast({
						title: err.data,
						icon: "none",
						duration: 2000
					});
					reject(err.data)
				} else {
					uni.showToast({
						title: '',
						duration: 2000,
						icon: "loading",
					});
					reject(new Error(''))
				}
			}
		});
	})
}

function postquery(url, params) {
	var Authorization = uni.getStorageSync('Authorization');
	return new Promise((resolve, reject) => {
		uni.request({
			url: url,//
			method: "POST",
			data: params,
			header: {
				'content-type': 'application/x-www-form-urlencoded',
				'Authorization': Authorization
			},
			success: (res) => {
				uni.hideLoading();
				if(res.data.code==200){
					resolve(res.data);
				}else{
					erry(res)
					reject(res.data)
				}
			},
			fail: (err) => {
				uni.hideLoading();
				if (err) {
					uni.showToast({
						title: err.data,
						icon: "none",
						duration: 2000
					});
					reject(err.data)
				} else {
					uni.showToast({
						title: '',
						icon: "none",
					});
					reject(new Error(''))
				}
			}
		});
	})
}
function erry(e){
	uni.showToast({
		title: e.data.msg,
		icon: "none",
	});
	console.log(e.data.msg)
	
	if(e.data.msg.indexOf('')!=-1){
		uni.removeStorageSync('Authorization');
		uni.navigateTo({
		    url: '/pages/index/login'
		});
	} 
}
